/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/

/* Front page */

.hero { 
    background-image: 
    linear-gradient(180deg, rgba(0, 0, 0, 0.40), rgba(0, 0, 0, 0.40)),
    url('../../images/home-hero.jpg');
    background-size: cover, cover;
    background-repeat: no-repeat; 
	height: 100vh;
	display: flex;
	justify-content: center;
	align-content: center;
	flex-direction: column;
		img {
			width: 275px; 
			height: 275px;
			margin-left: auto;
			margin-right: auto;
		}
}

.shop-container {
	margin-right: auto;
	margin-left: auto;
	width: 1120px;
	margin-bottom: 3rem;
		h2 {
			font-size: 2.4rem;
			text-transform: uppercase;
			font-family: $font__header;
			font-weight: normal;
			font-style: normal;
			color: $color__header;
			text-align: center;
			letter-spacing: 2px;
		}
}

.home .product-type-grid {
	display: flex;
	justify-content: space-between;
	text-align: center;
	width: 1120px;
		p {
			margin-bottom: 1.5rem;
		}

}

.home .product-type-box {
	flex:0 1 23.5%;
	max-width: 23.5%;
	border: 1px solid $color__rule;
	border-top-width: 5px;
	padding: 0.75rem;
	img {
		width: 70px;
		height: 70px;
	}
	a:hover,
	a:visited {
		color: $color__reverse;
	}
}

.home .product-type-button {
	background-color: $color__link;
	color: $color__reverse;
	font-family: $font__header;
	font-weight: normal;
    font-style: normal;
	text-transform: uppercase;
	text-decoration: none;
	padding: 0.75rem 1.5rem;
	font-size: 0.9rem;
}


.home .journal-container {
	margin-right: auto;
	margin-left: auto;
	width: 1120px;
	margin-bottom: 4.5rem;
	h2 {
		font-size: 2.4rem;
		text-transform: uppercase;
		font-family: $font__header;
		font-weight: normal;
		font-style: normal;
		color: $color__header;
		text-align: center;
		letter-spacing: 2px;
	}

	ul {
		list-style: none;
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 1120px;
		padding-left: 0;
		margin-left: 0;
		margin-bottom: 0;
		margin-top: 3rem;
			li {
				flex: 0 1 32%;
				max-width: 32;
				border: 1px solid black;
				text-transform: uppercase;
				font-family: $font__main;
				position: relative;
					.post-info {
						font-size: 0.75rem;
						padding: 0.75rem;
					}
						h3 {
							font-size: 1.3rem;
							font-family: $font__header;
							font-weight: bold;
							font-style: normal;
							line-height: 1.5rem;
							margin-top: 0;
							letter-spacing: 2px;
							margin-bottom: 3.75rem;
							a {
								text-decoration: none;
								color: $color__link;
							}
							a:hover {
								color: $color__text-main;
							}
						}
						img {
							height: 250px;
							max-width: 100%;
							position: relative;
						}
				}
		}
}

.frontpage-entries {
	display: flex;
	flex-direction: row wrap;
}

//Front Page Journal

.home .read-button {
	border: 1px solid $color__border;
	position: absolute;
	left: 0.75rem;
	bottom: 0.75rem;
	font-family: $font__header;
	font-size: 0.9rem;
	padding: 0.75rem 1.5rem;
	text-decoration: none;
		a {
			color: $color__border;
		}
}

.button:hover {
	background-color: $color__link;
}

.shop-heading {
	font-size: 2.4rem;
	text-transform: uppercase;
	font-family: $font__header;
}

//LATEST ADVENTURES 

.adventures-container {
	margin-right: auto;
	margin-left: auto;
	width: 1120px;
	margin-bottom: 4.5rem;
	h2 {
		font-size: 2.4rem;
		text-transform: uppercase;
		font-family: $font__header;
		font-weight: normal;
		font-style: normal;
		color: $color__header;
		text-align: center;
		letter-spacing: 2px;
	}
}

.adventure-posts {
	display: grid;
	flex-flow: column;
	width: 100%;
	height: 540px;
	background-color: blue;
	list-style: none;
	padding-left: 0;
	
}

