.canoe {
	background: url('../../images/adventure-photos/canoe-girl.jpg') no-repeat;
	background-size: cover;
	max-width: calc(100% / 2);
	height: 100%;
}
.friends {
	background: url('../../images/adventure-photos/beach-bonfire.jpg') no-repeat;
	background-size: cover;
	max-width: calc(100% / 2);
}
.mountain {
	background-image: url('../../images/adventure-photos/mountain-hikers.jpg');
	max-width: calc(100% / 3);
}
.night-sky {
	background-image: url('../../images/adventure-photos/night-sky.jpg');
	max-width: calc(100% / 3);
}

//ABOUT PAGE 

.page-template-about {
	.container {
		margin-left: auto;
		margin-right: auto;
		width: 1120px;
		font-size: 1rem;
	}
	
	.entry-title {
		font-family: $font__header;
		text-transform: uppercase;
		text-align: center;
		color: $color__reverse;
		font-size: 3rem;
		margin-top: auto;
		margin-bottom: auto;
	}

	.entry-content {
		max-width: 800px;
		margin: 3rem auto;
		line-height: $font__line-height-body;
		h2 {
			font-family: $font__header;
			text-transform: uppercase;
			text-align: center;
			font-size: 2rem;
		}	
	} 

	.entry-header {
		.container {
			margin-left: auto;
			margin-right: auto;
		}
	}
}

//SHOP STUFF//

.product-type-grid {
    display: flex;
    justify-content: space-between;
    text-align: center;
    width: 1120px;
        .product-type-box {
            flex-basis: 25%;
        }
        .img {
            width: 70px;
            height: 70px;
        }
}




//PRODUCT-TYPE PAGE

.tax-product-type {
	.page-title {
		margin-top: 0;
		margin-bottom: 1.5rem;
		font-family: $font__header;
		font-weight: bold;
		font-style: normal;
	}
	.taxonomy-description {
		text-align: center;
		border-bottom: 1px dashed $color__rule;
		margin-bottom: 1.5rem;
	}
}

.product-grid {
	display: flex;
    justify-content: space-between;
    text-align: center;
   	margin-bottom: 1.5rem;
	width: 1120px;
	margin-left: auto;
	margin-right: auto;
}

.product-box {
	width: 23%;
	flex: 0 1 23%;
	margin-bottom: 1.5rem;
	border: 1px solid $color__rule;
		h2 {
			font-size: 1rem;
			font-family: $font__main;
			font-weight: normal;
			font-style: normal;
		}
		p {
			text-decoration: none;
		}
}

.product-box .wp-post-image {
	left: 50%;
}


//ARCHIVE PRODUCTS PAGE 

.post-type-archive {
	.archive-product-grid {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		text-align: center;
		   margin-bottom: 1.5rem;
		width: 1120px;
		margin-left: auto;
		margin-right: auto;
	}

	.archive-product-box {
		flex: 0 1 23%;
		width: 23%;
		margin-bottom: 1.5rem;
		border: 1px solid $color__rule;
	}	

	.entry-header {
		height: 250px;
		background-color: $color__rule;
		position: relative;
		overflow: hidden;
		img.wp-post-image {
			max-height: 250px;
			position: absolute;
			max-width: none;
			top: 0;
			left: 0;
			width: auto;
			}
	}
}


.post-type-archive,
.tax-product-type {
	.entry-content {
		padding: 0;
		.entry-title {
			font-size: 1rem;
			font-family: $font__main;
			font-weight: 300;
			font-style: normal;
			margin-left: 0;
			display: inline;
			text-align: left;
			line-height: $font__line-height-body;
			background: $color__reverse; 	
		}
		.price {
			float: right;
			background: $color__reverse; 
		}
	}
	
	//DOT LEADERS 
	ul.leaders {
		max-width: 100%;
		padding: 0;
		overflow-x: hidden;
		list-style: none;
		padding: 0.75rem 1.5rem 0 1.5rem;
		margin-left: 0;
	}
	ul.leaders li:after {
		float: left;
		width: 0;
		white-space: nowrap;
		content:
	 ". . . . . . . . . . . . . . . . . . . . "
	 ". . . . . . . . . . . . . . . . . . . . "
	 ". . . . . . . . . . . . . . . . . . . . "
	 ". . . . . . . . . . . . . . . . . . . . "}
	ul.leaders span:first-child {
		margin: 0;
		background: white}
	ul.leaders span + span {
		float: right;
		// padding-left: 0.33em;
		background: white;
		position: relative;
		z-index: 1
	}
}



// JOURNAL


.blog {
	entry-header {
		position: relative; 
			
		h2 {
			background-color: $color__link;
			color: $color__reverse;
			font-family: $font__header;
			text-transform: uppercase;
			font-size: 2rem;
			left: 0;
			position: absolute;		
		}
		}	
	}

	.img {
		height: auto;
		max-width: 100%;
	}

